import styled from "styled-components";

export const AboutSection = styled.div`
  padding: 60px 0;
  visibility: hidden;
`;

export const AboutRow = styled.div`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
`;

export const AboutColumn = styled.div`
  display: flex;
  max-width: 100%;
  flex-basis: 100%;
  justify-content: center;
`;

export const TextWrapper = styled.div`
  max-width: 70%;
  padding-top: 0;
  padding-top: 60px;
  padding-bottom: 60px;
  text-align: center;

  @media screen and (max-width: 915px) {
    /* padding-bottom: 65px; */
  }

  @media screen and (max-width: 550px) {
    max-width: 95%;
  }
`;

export const TopLine = styled.h2`
  color: #2563eb;
  font-size: 35px;
  font-weight: 500;
`;

export const SubTitle = styled.div`
  margin-top: 20px;
  color: #000;
  font-weight: 400;
  line-height: 45px;
  font-size: 20px;
  color: #354d5c;

  @media screen and (max-width: 766px) {
    max-width: 95%;
    font-size: 16px;
  }
`;
