import React, { useRef } from "react";
import { useIntersection } from "react-use";
import gsap from "gsap";
import { Container } from "../../globalStyles";

import {
  AboutSection,
  AboutRow,
  AboutColumn,
  TextWrapper,
  TopLine,
  SubTitle,
} from "./About.elements";

function About() {
  const sectionAbout = useRef(null);

  const intersection = useIntersection(sectionAbout, {
    root: null,
    rootMargin: "0px",
    threshold: 0.55,
  });

  const fadeIn = (element) => {
    gsap.to(element, 1, {
      opacity: 1,
      visibility: "visible",
      y: -60,
      ease: "power4.out",
      stagger: {
        amount: 0.3,
      },
    });
  };
  const fadeOut = (element) => {
    gsap.to(element, 1, {
      opacity: 0,
      y: -20,
      ease: "power4.out",
      stagger: {
        amount: 0.3,
      },
    });
  };

  intersection && intersection.intersectionRatio < 0.55
    ? fadeOut(".fade-about")
    : fadeIn(".fade-about");

  return (
    <>
      <AboutSection id="about" ref={sectionAbout}>
        <Container>
          <AboutRow>
            <AboutColumn>
              <TextWrapper>
                <TopLine className="fade-about">Sobre Mi</TopLine>
                <SubTitle className="fade-about">
                  Soy Matias Nuñez Crippa, Analista en Sistemas de Computación
                  recibido en la Universidad Nacional de Misiones. Actualmente
                  estoy estudiando Ingeniería en Sistemas de Información.
                  Disfruto mucho del desarrollo web, en específico el desarrollo
                  Front-end. Constantemente me encuentro adquiriendo nuevos
                  conocimientos sobre tecnologías y diseño para brindar la mejor
                  experiencia de usuario posible a la hora de desarrollar.
                </SubTitle>
              </TextWrapper>
            </AboutColumn>
          </AboutRow>
        </Container>
      </AboutSection>
    </>
  );
}

export default About;
