import React, { useRef } from "react";
import { useIntersection } from "react-use";
import gsap from "gsap";
import { Container } from "../../globalStyles";
import gestionImg from "../../images/gestionImg.png";
import bitnetImg from "../../images/bitnetLogo.svg";

import {
  TextWrapper,
  ProjectHeading,
  ProjectSection,
  ProjectRow,
  ProjectColumn,
  ProjectCard,
  CardTopLine,
  CardHeader,
  CardImgWrapper,
  CardImg,
} from "./Projects.elements";

const Projects = () => {
  const sectionProjects = useRef(null);

  const intersection = useIntersection(sectionProjects, {
    root: null,
    rootMargin: "0px",
    threshold: 0.3,
  });

  const fadeIn = (element) => {
    gsap.to(element, 1, {
      opacity: 1,
      y: -60,
      ease: "power4.out",
      stagger: {
        amount: 0.4,
      },
    });
  };
  const fadeOut = (element) => {
    gsap.to(element, 1, {
      opacity: 0,
      y: -20,
      ease: "power4.out",
      stagger: {
        amount: 0.4,
      },
    });
  };

  intersection && intersection.intersectionRatio < 0.3
    ? fadeOut(".fade-In")
    : fadeIn(".fade-In");

  return (
    <>
      <Container>
        <ProjectSection ref={sectionProjects} id="projects">
          <TextWrapper>
            <ProjectHeading className="fade-In">Proyectos</ProjectHeading>
          </TextWrapper>
          <ProjectRow>
            <ProjectColumn>
              <ProjectCard className="bg-lightgreen shadow-md fade-In">
                <CardTopLine className="color-dark">Laravel</CardTopLine>
                <CardHeader className="color-dark">
                  Gestion Municipal de Pedidos
                </CardHeader>
                <CardImgWrapper>
                  <CardImg src={gestionImg}></CardImg>
                </CardImgWrapper>
              </ProjectCard>
            </ProjectColumn>
            <ProjectColumn>
              <ProjectCard className="bg-salmon  shadow-md fade-In">
                <CardTopLine className="color-dark">Laravel</CardTopLine>
                <CardHeader className="color-dark">BitNet</CardHeader>
                <CardImgWrapper>
                  <CardImg className="bitnetLogo" src={bitnetImg}></CardImg>
                </CardImgWrapper>
              </ProjectCard>
              <ProjectCard className="bg-deepblue shadow-md fade-In">
                <CardTopLine className="color-dark">React</CardTopLine>
                <CardHeader className="color-dark">Portfolio</CardHeader>
                <CardImgWrapper>
                  <CardImg src={gestionImg}></CardImg>
                </CardImgWrapper>
              </ProjectCard>
            </ProjectColumn>
          </ProjectRow>
        </ProjectSection>
      </Container>
    </>
  );
};

export default Projects;
